import React, { Component } from "react";
import { connect } from "react-redux";
import { Auth, Basic } from "../../../actions";
import {
  FC_GetAllTransfers,
  FT_GetAllTransfer,
} from "../../../actions/transfer.action";
import Button from "../../../components/Buttons/Button";
import DownloadLink from "../../../components/DownloadLink/DownloadLink";
import ExportToExcel from "../../../components/GenerateReport/ExportToExcel";
import TextInput from "../../../components/Inputs/TextInput";
import TableLoading from "../../../components/TableLoading/TableLoading";
import { StoreState } from "../../../reducers";
import { TransferReducerType } from "../../../reducers/transfer.reducer";
import {
  SchoolInfoType,
  TransferRequestType,
} from "../../../types/TransferTypes";
import { isEmptyOrSpaces, search } from "../../../utils/functions";
import LeaveForm from "./LeaveForm";

type TransferRequestsProps = {
  auth: Auth;
  basic: Basic;
  transfer: TransferReducerType;
  FC_GetAllTransfers: FT_GetAllTransfer;
};

type TransferRequestsState = {
  loading: boolean;
  error: {
    target: "main-error" | "main-success";
    msg: string;
  } | null;
  search: string;
  selectedUserId: string;
};

class TransferRequests extends Component<
  TransferRequestsProps,
  TransferRequestsState
> {
  constructor(props: TransferRequestsProps) {
    super(props);

    this.state = {
      loading: true,
      error: null,
      search: "",
      selectedUserId: "",
    };
  }

  componentDidMount = () => {
    if (
      this.props.transfer.allTransfers === null ||
      this.props.transfer.allTransfers.length === 0
    )
      this.props.FC_GetAllTransfers(
        (status: boolean, data: TransferRequestType[] | null, msg: string) => {
          this.setState({ loading: status });

          if (!status && data) this.setState({ loading: status });
          else if (!status && !isEmptyOrSpaces(msg))
            this.setState({
              error: { target: "main-error", msg: msg },
            });
        }
      );
    else if (this.state.loading === true) this.setState({ loading: false });
  };

  getTransferById = (transfer_id: string): TransferRequestType | undefined => {
    if (this.props.transfer.allTransfers)
      return this.props.transfer.allTransfers.find(
        (item) => item.teacher_transfer_id.toString() === transfer_id
      );
    return undefined;
  };

  FC_DisplaySchools = (schools: SchoolInfoType[]) => {
    return schools.map((item, index) => (
      <p>
        {index + 1} {item.school_name}
      </p>
    ));
  };
  render() {
    return (
      <>
        <div className="mt-3">
          <div className="border border-t mt-2 mb-1 flex items-center px-3 rounded">
            <div className="flex-1 pr-2">
              <TextInput
                error=""
                label="Search"
                type="search"
                name=""
                onChange={(e) => this.setState({ search: e.target.value })}
                placeholder="Search"
                value={this.state.search}
                size="sm"
              />
            </div>
            <div className="p-2">
              {this.props.transfer.allTransfers &&
                this.props.transfer.allTransfers.length >= 1 && (
                  <ExportToExcel
                    fileData={this.props.transfer.allTransfers.map((el) => ({
                      approved_school_id: el.approved_school_id,
                      Approved_school_name:
                        el.approved_school_info?.school_name,
                      Approved_school_code:
                        el.approved_school_info?.school_code,
                      Approved_school_district:
                        el.approved_school_info?.district_name,
                      position_code: el.position_code,
                      position_name: el.position_name,
                      qualification_name: el.qualification_name,
                      requested_comment: el.requested_comment,
                      school_from_name: el.school_from_info?.school_name,
                      school_from_code: el.school_from_info?.school_code,
                      school_from_district: el.school_from_info?.district_name,
                      teacher_id: el.teacher_id,
                      teacher_comment: el.teacher_comment,
                      teacher_last_name: el.teacher_last_name,
                      teacher_full_name: el.teacher_full_name,
                      teacher_phone_number: el.teacher_phone_number,
                      transfer_reason: el.transfer_reason,
                      transfer_type: el.transfer_type,
                    }))}
                    fileName={`INCOMING TRANSFERS`}
                    btnName="Download EXCEL"
                  />
                )}
            </div>
          </div>
          <div className="w-full overflow-x-auto mt-4">
            <table className="w-full">
              <thead className="bg-blue-50 text-blue-800 font-bold text-base text-left">
                <tr>
                  <th className="p-2 border">#</th>
                  <th className="p-2 border">Full Name</th>
                  <th className="p-2 border">Phone</th>
                  <th className="p-2 border">Current School</th>
                  <th className="p-2 border">Approved School</th>
                  {/* <th className="p-2 border">Approved Sector</th> */}
                  <th className="p-2 border">Position</th>
                  <th className="p-2 border">Qualification</th>
                  <th className="p-2 border"></th>
                </tr>
              </thead>
              <tbody>
                {this.state.loading ? (
                  <TableLoading cols={8} />
                ) : this.props.transfer.allTransfers === null ? (
                  <tr className="border-t border-b">
                    <td
                      colSpan={8}
                      className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      Something went wrong! Please try again.
                    </td>
                  </tr>
                ) : this.props.transfer.allTransfers.length <= 0 ? (
                  <tr>
                    <td
                      colSpan={8}
                      className="p-6 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                    >
                      No results found
                    </td>
                  </tr>
                ) : (
                  search(
                    this.props.transfer.allTransfers,
                    this.state.search
                  ).map((element: TransferRequestType, index: number) => {
                    return (
                      <tr
                        key={element.teacher_transfer_id}
                        className="border-t border-b hover:bg-gray-50"
                      >
                        <td className="p-2 border">{index + 1}</td>
                        <td className="p-2 border">
                          {element.teacher_full_name}
                        </td>
                        <td className="p-2 border">
                          {element.teacher_phone_number}
                        </td>
                        <td className="p-2 border">
                          {element.school_from_info
                            ? element.school_from_info.school_name
                            : ""}
                        </td>
                        <td className="p-2 border">
                          {element.requested_school_info
                            ? this.FC_DisplaySchools(
                                element.requested_school_info
                              )
                            : ""}
                        </td>

                        <td className="p-2 border">
                          {element.position_code} {element.position_name}
                        </td>
                        <td className="p-2 border">
                          {element.qualification_name}
                        </td>
                        {/* <td className="p-2 border">
                          {element.requested_school_info
                            ? element.requested_school_info.sector_name
                            : ""}
                        </td> */}

                        {this.state.selectedUserId ===
                        element.teacher_transfer_id.toString() ? (
                          <td className="p-2 border">
                            <LeaveForm
                              selectedUserId={this.state.selectedUserId}
                              setSelectedUserId={(value: string) =>
                                this.setState({ selectedUserId: value })
                              }
                            />
                          </td>
                        ) : (
                          <>
                            {/* <td className="p-2 border"></td> */}
                            <td className="p-2 border-1 flex gap-2">
                              {element.mayor_letter === null ||
                              isEmptyOrSpaces(element.mayor_letter) ? (
                                <Button
                                  size="sm"
                                  theme="success-light"
                                  onClick={(e) =>
                                    this.setState({
                                      selectedUserId:
                                        element.teacher_transfer_id.toString(),
                                    })
                                  }
                                >
                                  Upload signed letter
                                </Button>
                              ) : (
                                element.requested_status
                              )}

                              {element.mayor_letter === null ||
                              isEmptyOrSpaces(element.mayor_letter) ? (
                                <DownloadLink
                                  linkFor="transfer"
                                  first_id={element.teacher_transfer_id}
                                  last_id={element.teacher_id}
                                  title={"Transfer letter"}
                                />
                              ) : (
                                element.mayor_letter &&
                                !isEmptyOrSpaces(element.mayor_letter) && (
                                  <DownloadLink
                                    linkFor="uploaded"
                                    first_id={element.mayor_letter}
                                    last_id={""}
                                    title={"Transfer letter"}
                                  />
                                )
                              )}
                            </td>
                          </>
                        )}
                      </tr>
                    );
                  })
                )}
              </tbody>
            </table>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  transfer,
}: StoreState): { auth: Auth; basic: Basic; transfer: TransferReducerType } => {
  return { auth: auth, basic: basic, transfer };
};

export default connect(mapStateToProps, {
  FC_GetAllTransfers,
})(TransferRequests);
